import { createApp } from 'vue';

import router from './router.js';
import App from './App.vue';
import BaseImage from './components/ui/BaseImage.vue'
import SquareSvg from './components/svg/SquareSvg.vue';
import LineSvg from './components/svg/LineSvg.vue';
import SvgRight from './components/svg/SvgRight.vue';
import SvgLeft from './components/svg/SvgLeft.vue';
import SvgArrow from './components/svg/SvgArrow';
import FacebookSvg from './components/svg/FacebookSvg.vue';
import InstagramSvg from './components/svg/InstagramSvg.vue';
import TwitterSvg from './components/svg/TwitterSvg.vue';
import GithubSvg from './components/svg/GithubSvg.vue';
import HotmailSvg from './components/svg/HotmailSvg.vue';
import GmailSvg from './components/svg/GmailSvg.vue';

const app = createApp(App)

app.use(router);

app.component('square-svg', SquareSvg);
app.component('base-image', BaseImage);
app.component('line-svg', LineSvg);
app.component('svg-right', SvgRight);
app.component('svg-left', SvgLeft);
app.component('svg-arrow', SvgArrow);
app.component('svg-facebook', FacebookSvg );
app.component('svg-instagram', InstagramSvg);
app.component('svg-twitter', TwitterSvg);
app.component('svg-github', GithubSvg);
app.component('svg-hotmail', HotmailSvg);
app.component('svg-gmail', GmailSvg);

app.mount('#app');
